<script>
import { rubbishNftAbi, rubbishBeastAbi } from "../assets/abi1";

export default {
  data() {
    return {
      web3: "",
      rubbishContract: "",
      beastContract: "",
      rubbishAddress: "0x8b44b715004020773e8da1cd730de2f47c7d88b8",
      beastAddress: "0xc1AeA99329A439e30d622210c2c0782797C98C48",
      address: "",
      tokenIdArr: [],
    };
  },
  methods: {
    connectWallet() {
      if (window.ethereum) {
        window.ethereum.enable().then((res) => {
          this.web3 = new this.Web3(window.web3.currentProvider);
          this.web3.eth.getAccounts().then((res) => {
            this.address = res[0];
          });
        });
      } else {
        alert("Please Install MetaMask!!!");
      }
    },
    async initContracts() {
      this.rubbishContract = await new this.web3.eth.Contract(
        rubbishNftAbi,
        this.rubbishAddress
      );
      this.beastContract = await new this.web3.eth.Contract(
        rubbishBeastAbi,
        this.beastAddress
      );
      let res = await this.rubbishContract.methods
        .tokensOfOwner(this.address)
        .call();
      this.tokenIdArr = res;
    },
    async approvelAll() {
      this.rubbishContract = await new this.web3.eth.Contract(
        rubbishNftAbi,
        this.rubbishAddress
      );
      this.beastContract = await new this.web3.eth.Contract(
        rubbishBeastAbi,
        this.beastAddress
      );
      let res = await this.rubbishContract.methods
        .tokensOfOwner(this.address)
        .call();
      this.tokenIdArr = res;
      if (this.tokenIdArr != []) {
        let data = await this.rubbishContract.methods
          .setApprovalForAll(this.beastAddress, true)
          .encodeABI();
        let txDataA = {
          // nonce: web3.utils.toHex(nonce),//nonce
          to: this.rubbishAddress, //router
          from: this.address, //
          data: data, //
          // gas: 500000
          // value:amoutA,//ETH
        };
        let res = this.web3.eth.sendTransaction(txDataA);
        res
          .then((res) => {
            this.$Modal.info({
              title: "Info",
              content: "approvel all successfully!",
              okText: "confirm",
            });
          })
          .catch((err) => {
            this.$Modal.warning({
              title: "warning",
              content: "approve failed!",
              okText: "confirm",
            });
          });
      } else {
        this.$Modal.warning({
          title: "warning",
          content: "approve fail please refresh your website!!",
          okText: "confirm",
        });
      }
    },
    async rubbishClaim() {
      this.rubbishContract = await new this.web3.eth.Contract(
        rubbishNftAbi,
        this.rubbishAddress
      );
      let res = await this.rubbishContract.methods
        .tokensOfOwner(this.address)
        .call();
      this.tokenIdArr = res;
      if (this.tokenIdArr.length >= 1) {
        let claimAmount = parseInt(this.tokenIdArr.length / 4);
        this.tokenIdArr = this.tokenIdArr.slice(0, claimAmount * 4);
        let data = await this.beastContract.methods
          .rubbishClaim(this.tokenIdArr)
          .encodeABI();
        let txDataA = {
          // nonce: web3.utils.toHex(nonce),//nonce
          to: this.beastAddress, //router
          from: this.address, //
          data: data, //
          // gas: 500000
          // value:amoutA,//ETH
        };
        let res = this.web3.eth.sendTransaction(txDataA);
        res.then((res) => {
          this.$Modal.info({
            title: "Info",
            content: "mint beast~~ successfully!",
            okText: "confirm",
          });
        });
      } else {
        this.$Modal.warning({
          title: "warning",
          content: "claim failed please check your account!!!",
          okText: "confirm",
        });
      }
    },
  },
  mounted() {
    this.connectWallet();
    // this.initContracts();
  },
};
</script>

